import React, { useRef, useEffect, useState } from "react";
import { motion, useAnimation, useInView } from "framer-motion";
import "../styles/ExperiencesTimeline.css";

const experiences = [
  {
    year: "2024 (Present)",
    short: "Lead Developer at Modern Web App",
    long: "Leading the development of a modern web application using NextJS and convext, implementing features like real-time order tracking and inventory management. Introduced automated testing and CI/CD pipelines for reliable deployments.",
  },
  {
    year: "Mid 2024",
    short: "Intern at GP Ahmedabad",
    long: "Worked on digital transformation for the Computer Engineering department. Built a faculty and record management portal, digitized paper records, and improved department workflows.",
  },
  // Add more experiences as needed
];

export const ExperiencesTimeline = () => {
  const timelineRef = useRef(null);
  const [hovered, setHovered] = useState(null);
  const controls = useAnimation();
  const isInView = useInView(timelineRef, { once: false, margin: "-100px" });
  
  useEffect(() => {
    if (isInView) controls.start({ scaleY: 1 });
    else controls.start({ scaleY: 0 });
  }, [isInView, controls]);

  return (
    <div className="experiences-section">
      <h1 className="timeline-title">Experience</h1>
      <div className="timeline-container">
        <motion.div
          className="timeline-bar"
          ref={timelineRef}
          initial={{ scaleY: 0 }}
          animate={controls}
          transition={{ duration: 0.8, ease: "easeInOut" }}
        />
        <div className="timeline-items">
          {experiences.map((exp, idx) => (
            <motion.div
              className="timeline-item"
              key={idx}
              initial={{ opacity: 0, x: -50 }}
              whileInView={{ opacity: 1, x: 0 }}
              viewport={{ once: true, amount: 0.3 }}
              transition={{ duration: 0.7, delay: idx * 0.2 }}
              onMouseEnter={() => setHovered(idx)}
              onMouseLeave={() => setHovered(null)}
            >
              <div className="timeline-dot" />
              <div className="timeline-content">
                <div className="timeline-year">{exp.year}</div>
                <div className="timeline-desc">
                  {hovered === idx ? (
                    <motion.div
                      className="timeline-long"
                      initial={{ opacity: 0, y: 10 }}
                      animate={{ opacity: 1, y: 0 }}
                      exit={{ opacity: 0, y: 10 }}
                      transition={{ duration: 0.3 }}
                    >
                      {exp.long}
                    </motion.div>
                  ) : (
                    <div className="timeline-short">{exp.short}</div>
                  )}
                </div>
              </div>
            </motion.div>
          ))}
        </div>
      </div>
    </div>
  );
};

export default ExperiencesTimeline;
